







import { Vue, Component, Prop } from "vue-property-decorator";

@Component
export default class TextBox extends Vue {

	@Prop()
	title?: string;

	@Prop({default: ""})
	value!: string;

	@Prop()
	inputClass?: string;

	get inputVal(): string {
		return this.value;
	}

	set inputVal(newVal: string) {
		this.$emit('input', newVal);
	}
}

